.ToDo{
    padding: 5px;
    margin: auto;
    margin-bottom: 5px;
    background-color: #57CC99;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    border: solid 2px #0b4870;
    border-bottom: 4px  solid #22577A;
    border-radius: 10px;
    font-size: 1.5rem;
    font-weight: bold;
    width: 98%;
}

.id{
    color: #0B0500;
    width: 30px;
    margin: auto 10px;
}

.taskUnfinish{
    color: #0B0500;
    width:80%;
    text-align: justify;
}

.taskFinish{
    color: #086e10;  
    width:80%;
    text-align: justify;
    /* 
    text-decoration-line: line-through;
    text-decoration-style: double;
    text-decoration-color: #086e10;
    */
    text-decoration: line-through solid #086e10;
    text-decoration-thickness: 15%;
}

.checkBox{
    cursor: pointer;
    margin: auto 5px;
}
.checkBox:hover{
    transform:scale(1.4,1.4);
}

.deleteBtn{
    margin: auto 0;
    width: 90px;
    height: 35px;
    text-align: center;
    font-size: 1.2rem;
    border-radius: 10px;    
}

.tickingTime{
    margin: auto;
    margin-top: -5px;
    margin-bottom: 5px;
    padding: 3px;
    display: flex;
    justify-content: space-between;
    width: 50%;
    min-width: 320px;
    border: solid 3px  #22577A;
    border-top: 0;
    border-radius: 10px;
    font-weight: bold;
}

.calendarBtn{
    min-width: 40px;
    border-radius: 6px;
}