.ToDoList{    
    background-color: #80ED99;
    height: 100%;
    width: 90%;
    max-width: 800px;
    padding-bottom: 10px;
    margin: auto;
    border-radius: 10px;
}

.appHeader{
    height: 120px;
    margin-bottom: 5px;;
    border: 2px solid black;
    border-radius: 10px;
}
.list{
    max-height: calc(90vh - 159px);
    overflow: auto;
}